.ui5strapTabContainer{
	position:relative;
	min-height:100px;
	overflow:hidden;
}

.ui5strapTabContainer-flag-FullHeight{
	height:100%;
}

.ui5strapTabContainer-pane{
	position:absolute;
	width:100%;
	height:100%;
	top:0;
}
