/*
* Scroll Container
*/
.ui5strapScrollContainer{
	width:100%; 
	height:100%;
	-webkit-overflow-scrolling: touch;
	
	overflow-x: hidden;
	overflow-y: hidden;
}

.ui5strapScrollContainer-flag-Horizontal{
	overflow-x: scroll;
}

.ui5strapScrollContainer-flag-Vertical{
	overflow-y: scroll;
}

.sapUiMedia-Std-Desktop .ui5strapScrollContainer-flag-Horizontal{
	overflow-x: auto;
}

.sapUiMedia-Std-Desktop .ui5strapScrollContainer-flag-Vertical{
	overflow-y: auto;
}